#cloudContainer {
  width: 100%;
  height: 100vh;
  min-height: 700px;
  min-width: 700px;
  background-image: linear-gradient(#141e2f, #283c5f 20%, #5a86d5 60%, #6495ed 80%);
  position: relative;
}

.cloud-parallax {
  position: absolute;
  width: 100%;
  height: 100vh;
  left: 0;
  top: 0;
}